.header {
  // display: none !important;
  height: 100vh;
  background-image: url(../images/header-bg.png);
  background-size: cover;
  background-position: center;
  
  
  &__content {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
  }

  &__bottom {
    padding-bottom: 40px;

    @include onTablet {
      padding-bottom: 64px;
    }
    
  }

  &__title {
    margin: 0;
    color: $white-color;
    font-size: 36px;
    font-style: normal;
    font-weight: 700;
    line-height: 100%;

    @include onTablet {
      font-size: 40px;  //new
    }
  }
}
