.nav {

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 32px;
  }

  &__link {
    color: inherit;
    text-decoration: none;
    font-size: 22px;
    font-weight: 700;
    line-height: 22px;
    position: relative;

    &::after {
      content: " ";
      display: block;
      position: absolute;
      bottom: -8px;
      height: 1px;
      width: 100%;
      background-color: $main-color;

      transition: transform 0.3s;
      transform: scale(0);
      transform-origin: left;
    }

    &:hover::after {
      transform: scale(1);

    }

  }

}
